import React from 'react';
import {getLang} from "@web3game/lang";
import {useVPStyle} from "@web3game/common/components/viewPort/getElStyle";
import {compose} from "@web3game/common/utils/compose";
import {inject} from "mobx-react";
import {GameUIScene} from "@web3game/common/utils/constants";
import {GameUIStore} from "@web3game/common/stores/GameUIStore";

type DescriptionButtonProps = {
    onShowDescription: () => void;
    isPlayerActive?: boolean;
};

export function DescriptionButtonPure(
    {
        onShowDescription,
        isPlayerActive = false,
    }: DescriptionButtonProps,
) {
    if (isPlayerActive) {
        return null;
    }

    const buttonSize = useVPStyle({
        position: 'absolute',
        fontSize: 26,
        width: 280,
        height: 120,
        top: 300,
        left: 150,
    });

    return (
        <div
            className="gameDescriptionButton"
            onClick={onShowDescription}
            style={buttonSize}
        >
            {getLang().OPEN_BOX_PAGE.DESCRIPTION}
        </div>
    );
}

type GameDescriptionButtonInjectedProps = {
    gameUIStore?: GameUIStore;
};


export const DescriptionButton = compose<DescriptionButtonProps>(
    React.memo,
    inject(
        (
            { gameUIStore }: GameDescriptionButtonInjectedProps,
        ) => {
            const isPlayerActive = gameUIStore && gameUIStore.scene === GameUIScene.gamePlayer;

            return {
                isPlayerActive,
            };
        },
    )
)(DescriptionButtonPure);
