import React, {useEffect, useState} from 'react';
import {inject} from "mobx-react";
import {Popup} from "@web3game/common/components/popups/Popup";
import {GameRecord} from "@web3game/typings/openBox";
import {GamePlayerStore} from "../stores/GamePlayerStore";
import {getLang} from "@web3game/lang";


type GameDescriptionPopupProps = {
    gameRecord?: GameRecord;
};

export function GameDescriptionPopupPure() {
    const [htmlContent, setHtmlContent] = useState('');
    useEffect(() => {
        (async () => {
            const html = await fetch(getLang().HTML_FILES.GAME_DESCRIPTION);
            setHtmlContent(await html.text());
        })();
    }, []);

    return (
        <Popup
            title={getLang().OPEN_BOX_PAGE.DESCRIPTION}
            className="gameDescriptionPopup"
        >
            <div dangerouslySetInnerHTML={{ __html: htmlContent }} />
        </Popup>
    );
}

type GameResultPopupInjectedProps = {
    gamePlayerStore?: GamePlayerStore;
    web3?: any;
};

export const GameDescriptionPopup = inject(
    ({ gamePlayerStore, web3 }: GameResultPopupInjectedProps) => (
        {
            gameRecord: gamePlayerStore ? gamePlayerStore.gameRecord : null,
            web3,
        }
    ),
)(GameDescriptionPopupPure);
