import React, {useContext} from 'react';
import {PopupManager} from "@web3game/common/components/popups/PopupManger";
import {ViewPortSizeContext} from "@web3game/common/components/viewPort/viewPortSizeHelper";
import BackgroundUrl from '@web3game/common/styles/images/beijingtu.png';
import {PopupElement} from "@web3game/typings/common";
import {useVPStyle} from "@web3game/common/components/viewPort/getElStyle";

import {QuitGameButton} from "./QuitGameButton";
import {HistoryButton} from "./HistoryButton";
import {DescriptionButton} from "./DescriptionButton";

export type GameLayoutProps = {
    children?: React.ReactElement | React.ReactElement[];
    onShowHistory: () => void;
    onShowDescription: () => void;
    onQuitGame?: () => void;
    onQuitPlayer?: () => void;
    popups?: PopupElement[];
};

export function GameLayout(
    {
        children,
        onShowHistory,
        popups = [],
        onQuitGame,
        onQuitPlayer,
        onShowDescription,
    }: GameLayoutProps,
) {
    const {
        adjustedHeight,
        adjustedWidth,
    } = useContext(ViewPortSizeContext);

    const applicationSize = {
        width: adjustedWidth,
        height: adjustedHeight,
    };

    const gameContentStyle = useVPStyle({
        paddingTop: 460,
    });

    return (
        <div className="gameApplication" style={applicationSize}>
            <img src={BackgroundUrl} alt="background" className="backgroundImage" />
            <HistoryButton onShowHistory={onShowHistory} />
            <DescriptionButton onShowDescription={onShowDescription} />
            <QuitGameButton
                onQuitGame={onQuitGame}
                onQuitPlayer={onQuitPlayer}
            />

            <div className="gameDesktop">
                <div className="gameDesktopContent" style={gameContentStyle}>
                    {children}
                </div>
            </div>

            <PopupManager popups={popups} />
        </div>
    );
}
